<h2 id="collapse-shu-xing"><a class="header-anchor" href="#collapse-shu-xing" aria-hidden="true">¶</a> collapse 属性</h2>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>accordion</td>
<td>手风琴模式</td>
<td>boolean</td>
<td>false</td>
<td></td>
</tr>
<tr>
<td>value</td>
<td>展开的 panel 的<code class="code_inline">index</code>值(如果是手风琴模式，则为单个 number 值，否则为数组，支持<code class="code_inline">v-model</code>)</td>
<td>number/array</td>
<td>—</td>
<td>-</td>
</tr>
<tr>
<td>simple</td>
<td>是否为简洁模式</td>
<td>boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>hide-arrow</td>
<td>是否隐藏箭头，全局配置，其下的 panel 都不展示箭头</td>
<td>boolean</td>
<td>—</td>
<td>false</td>
</tr>
</tbody>
</table>
<h2 id="collapse-shi-jian"><a class="header-anchor" href="#collapse-shi-jian" aria-hidden="true">¶</a> collapse 事件</h2>
<table class="table"><thead>
<tr>
<th>事件名</th>
<th>说明</th>
<th>参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>on-change</td>
<td>当展开的 panel 改变时触发</td>
<td>展开的 panel 的<code class="code_inline">index</code></td>
</tr>
</tbody>
</table>
<h2 id="collapse-slots"><a class="header-anchor" href="#collapse-slots" aria-hidden="true">¶</a> collapse slots</h2>
<table class="table"><thead>
<tr>
<th>名称</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>default</td>
<td>插入 panle</td>
</tr>
</tbody>
</table>
<h2 id="panel-shu-xing"><a class="header-anchor" href="#panel-shu-xing" aria-hidden="true">¶</a> panel 属性</h2>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>title</td>
<td>显示的标题</td>
<td>string</td>
<td>-</td>
<td></td>
</tr>
<tr>
<td>index</td>
<td>标识当前 panel，需要唯一</td>
<td>number</td>
<td>—</td>
<td>-</td>
</tr>
<tr>
<td>hide-arrow</td>
<td>是否隐藏箭头，配置这个值后，collapse 的<code class="code_inline">hide-arrow</code>将失效</td>
<td>boolean</td>
<td>—</td>
<td>false</td>
</tr>
</tbody>
</table>
<h2 id="panel-slots"><a class="header-anchor" href="#panel-slots" aria-hidden="true">¶</a> panel slots</h2>
<table class="table"><thead>
<tr>
<th>名称</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>default</td>
<td>panel 展开后的内容</td>
</tr>
<tr>
<td>title</td>
<td>自定义 panel 标题</td>
</tr>
</tbody>
</table>
</section></template>

<script>
    export default {
        data() {
            return {
                value1: null,
                value2: null
            }
        }
    }
</script>


